import "./PopularBuilds.css"

import BuildFilter from "../../containers/BuildFilter/BuildFilter";
import BuildTable from "../../containers/BuildTable/BuildTable";
import { useState } from "react";

export default function PopularBuilds() {
  const [godId, setGodId] = useState<number | undefined>(undefined);
  const [gameModeId, setGameModeId] = useState<number | undefined>(undefined);

  const onGodSelected = (godId: number | undefined) => {
    setGodId(godId);
  };

  const onGameModeSelected = (gameModeId: number | undefined) => {
    setGameModeId(gameModeId);
  };

  return (
    <div className="page-container">
      <h2 className="page-title">Popular Builds</h2>
      <br/>
      <BuildFilter onGodSelected={onGodSelected} onGameModeSelected={onGameModeSelected} />
      <br/><br/><br/>
      <BuildTable godId={godId} gameModeId={gameModeId}/>
      <br/><br/>
    </div>
  );
}
