import TextField from "@mui/material/TextField";
import Autocomplete from "@mui/material/Autocomplete";
import GodData from "../../offline_data/gods.json";

type GodSelectorProps = {
  onGodSelected: (godId: number) => void;
};

export default function GodSelector(props: GodSelectorProps) {
  const gods = GodData.map((god: any) => ({ id: god.Id, name: god.Name }));

  return (
    <Autocomplete
      disablePortal
      id="god-selector"
      options={gods}
      isOptionEqualToValue={(option, value) => option.id === value.id}
      sx={{ width: 300 }}
      onChange={(_, value) => props.onGodSelected(value?.id)}
      getOptionLabel={(god) => god.name}
      renderInput={(params) => <TextField {...params} label="God" />}
    />
  );
}
