import TextField from "@mui/material/TextField";
import Autocomplete from "@mui/material/Autocomplete";
import GameModeData from "../../offline_data/game_modes.json";

type GameModeSelectorProps = {
  onGameModeSelected: (godId: number) => void;
};

export default function GameModeSelector(props: GameModeSelectorProps) {
  //  const gameModes = GameModeData.map((gameMode: any) => ({ id: gameMode.Id, name: gameMode.Name }));

  const gameModes = GameModeData.filter((gameMode: any) => !gameMode.Name.includes("Console")).map((gameMode: any) => ({ id: gameMode.Id, name: gameMode.Name }));

  return (
    <Autocomplete
      disablePortal
      autoHighlight={true}
      id="game-mode-selector"
      options={gameModes}
      isOptionEqualToValue={(option, value) => option.id === value.id}
      sx={{ width: 300 }}
      onChange={(_, value) => props.onGameModeSelected(value?.id)}
      getOptionLabel={(gameMode) => gameMode.name}
      renderInput={(params) => <TextField {...params} label="Game Mode" />}
    />
  );
}
