import { useState } from "react";
import { useMediaQuery } from "react-responsive";
import GameModeSelector from "../../components/GameModeSelector/GameModeSelector";
import GodSelector from "../../components/GodSelector/GodSelector";
import GodData from "../../offline_data/gods.json";

import "./BuildFilter.css";

type BuildFilterProps = {
  onGodSelected: (godId: number | undefined) => void;
  onGameModeSelected: (godId: number | undefined) => void;
};

interface God {
  Id: number;
  Name: string;
  IconUrl: string;
}

export default function BuildFilter(props: BuildFilterProps) {
  const [currentGod, setCurrentGod] = useState<God | undefined>(undefined);
  const isMobile = useMediaQuery({ query: `(max-width: 760px)` });

  const onGodSelected = (godId: number | undefined) => {
    const god = godId ? GodData.find((god) => god.Id === godId) : undefined;
    setCurrentGod(god);
    props.onGodSelected(godId);
  };

  return (
    <div className="main-container">
      <div className="god-portrait-container">{getGodPortrait(isMobile, currentGod)}</div>
      <div className="filter-options-container">
        <GameModeSelector onGameModeSelected={props.onGameModeSelected} />
        <br />
        <GodSelector onGodSelected={onGodSelected} />
      </div>
    </div>
  );
}

function getGodPortrait(isMobile: boolean, god: God | undefined) {
  return (
    <img
      className="god-portrait"
      src={god?.IconUrl ?? ""}
      alt={god?.Name}
      style={{
        display: isMobile ? "none" : (god ? "block" : "none"),
      }}
    />
  );
}
