import BuildFilter from "../../containers/BuildFilter/BuildFilter";

export default function BuildSearch() {
  return (
    <div className="page-container">
      <h2 className="page-title">Build Search</h2>
      <br/>
      <BuildFilter onGodSelected={onGodSelected} onGameModeSelected={onGameModeSelected} />
    </div>
  );
}

function onGameModeSelected(gameModeId: number | undefined) {
  console.log(gameModeId);
}

function onGodSelected(godId: number | undefined) {
  console.log(godId);
}
