import { useEffect, useState } from "react";
import items from "../../offline_data/items.json";

import "./BuildTable.css";

type BuildTableProps = {
  godId: number | undefined;
  gameModeId: number | undefined;
};

interface IGodBuildCollection {
  builds: GodBuild[];
  relics: string[];
}

interface IGodBuild {
  starterItem: string;
  items: string[];
}

interface IItem {
  Id: number;
  Name: string;
  IconUrl: string;
}

class GodBuildCollection implements IGodBuildCollection {
  builds: GodBuild[];
  relics: string[];

  constructor(builds: GodBuild[], relics: string[]) {
    this.builds = builds;
    this.relics = relics;
  }
}

class GodBuild implements IGodBuild {
  starterItem: string;
  items: string[];

  constructor(starterItem: string, items: string[]) {
    this.starterItem = starterItem;
    this.items = items;
  }
}

export default function BuildTable(props: BuildTableProps) {
  const [buildCollection, setBuildCollection] = useState<GodBuildCollection | undefined>(undefined);

  useEffect(() => {
    async function getBuildCollectionAsync() {
      if (!props.godId || !props.gameModeId) {
        setBuildCollection(undefined);
        return;
      }
      console.log("Getting build collection for god " + props.godId + " in game mode " + props.gameModeId);
      const data = await getGodBuildCollectionAsync(
        props.gameModeId,
        props.godId
      );
      setBuildCollection(data);
    }
    getBuildCollectionAsync();
  }, [props.godId, props.gameModeId]);

  if (!buildCollection) {
    return props.godId && props.gameModeId ? <div>No builds found!</div> : <div></div>;
  }

  const buildRows = buildCollection.builds.map((build) => {
    return (
      <tr key={build.starterItem}>
        <td className="starter-item-container">
          {getItemIcon(build.starterItem)}
        </td>
        <td className="item-container">{getItemIcons(build.items)}</td>
      </tr>
    );
  });

  return (
    <div>
      <table>
        <thead>
          <tr>
            <th>Starter</th>
            <th>Items</th>
          </tr>
        </thead>
        <tbody>{buildRows}</tbody>
      </table>
    </div>
  );
}

function getItemIcon(itemId: string) {
  const item = items.find((item) => item.Id === Number.parseInt(itemId)) as
    | IItem
    | undefined;
  return getItemPortrait(item);
}

function getItemIcons(itemIds: string[]) {
  return itemIds.map((itemId) => {
    return getItemIcon(itemId);
  });
}

function getRandomInt(max: number) {
  return Math.floor(Math.random() * max);
}

function getItemPortrait(item: IItem | undefined) {
  return (
    <img
      className="item-portrait"
      key={getRandomInt(100000)}
      src={item?.IconUrl ?? ""}
      alt={item?.Name}
      style={{
        display: item ? "block" : "none",
      }}
    />
  );
}

async function getGodBuildCollectionAsync(gameModeId: number, godId: number) {
  const godBuildCollectionResponse = await fetch(
    `https://raw.githubusercontent.com/GowtherBot/SmiteGodBuilds/main/${gameModeId}/${godId}.json`
  );

  if (godBuildCollectionResponse.status === 404) {
    return undefined;
  }

  const godBuildCollectionJson = await godBuildCollectionResponse.json();

  const relics = godBuildCollectionJson.Relics;

  const builds = godBuildCollectionJson.Builds.map((build: any) => {
    return new GodBuild(build.StarterItem, build.Items);
  });

  return new GodBuildCollection(builds, relics);
}
